<template>
  <div id="app">
    <div class="App">
      <PageHead v-if="isRouterAlive"></PageHead>
      <router-view/>
    </div>
    <PageFootr></PageFootr>
  </div>
</template>

<script>
import PageHead from '@/components/Page_Head.vue'
import PageFootr from '@/components/Page_Footer.vue'
export default {
  components: {
    PageHead,
    PageFootr
  },
  data(){
    return{
      isRouterAlive:true
    }
  },
  methods:{
    reload(){
      this.isRouterAlive = false;
      this.$nextTick(function(){
        this.isRouterAlive = true;
      })
    }
  },
  mounted(){
    this.$bus.$on('reload', ()=> {
      this.reload();
    })
  }
}
</script>


