<template>
    <div>
        <h2>付款</h2>
        <div class="payForm">
            <el-form :model="payFrom" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <div class="title">卡片類型</div>
                <el-form-item prop="cardType" class="radioGrunp">
                    <el-radio-group v-model="payFrom.cardType">
                    <el-radio label="master" class="radioItem">
                        <img width="70" src="data:image/svg+xml;utf8;base64," />
                    </el-radio>
                    <el-radio label="visa" class="radioItem">
                        <img width="70" src="data:image/svg+xml;utf8;base64," />
                    </el-radio>
                    </el-radio-group>
                </el-form-item>
                <div class="title">信用卡資料</div>
                <div class="row">
                    <el-form-item label="信用卡卡號" required>
                        <div class="cardNumber">
                            <el-form-item prop="firstCol">
                                <el-input maxlength=4 v-model.number="payFrom.firstCol"></el-input>
                            </el-form-item>
                            <span>-</span>
                            <el-form-item prop="secondCol">
                                <el-input maxlength=4 v-model.number="payFrom.secondCol"></el-input>
                            </el-form-item>
                            <span>-</span>
                            <el-form-item prop="threeCol">
                                <el-input maxlength=4 v-model.number="payFrom.threeCol"></el-input>
                            </el-form-item>
                            <span>-</span>
                            <el-form-item prop="forthCol">
                                <el-input maxlength=4 v-model.number="payFrom.forthCol"></el-input>
                            </el-form-item>
                        </div>
                    </el-form-item>
                    <div class="subRow">
                        <el-form-item label="有效期限" prop="ValidityPeriod">
                            <el-select v-model="payFrom.ValidityPeriod" placeholder="请选择">
                                <el-option
                                v-for="(item,index) in ValidityDate"
                                :key="index"
                                :label="item.year+'/'+item.month"
                                :value="item.year+'/'+item.month">
                                {{item.year}}/{{item.month}}
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="授權碼" prop="AuthorizationCode">
                            <el-input maxlength=3 v-model.number="payFrom.AuthorizationCode"></el-input>
                        </el-form-item>
                    </div>
                </div>
                <div class="submitBtn">
                    <el-button type="primary" @click="submitForm('ruleForm')">確認送出</el-button>
                </div>

            </el-form>
        </div>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        payFrom: {
            cardType:'',
            AuthorizationCode:'',
            ValidityPeriod:'',
            firstCol:"",
            secondCol:"",
            threeCol:"",
            forthCol:""
        },
        ValidityDate:[],
        rules:{
            firstCol: [
                { required: true, message: '请輸入卡號', trigger: 'blur' },
                { type: 'number', message: '必須為數字'},
            ],
            secondCol: [
                { required: true, message: '请輸入卡號', trigger: 'blur' },
                { type: 'number', message: '必須為數字'},
            ],
            threeCol: [
                { required: true, message: '请輸入卡號', trigger: 'blur' },
                { type: 'number', message: '必須為數字'},
            ],
            forthCol: [
                { required: true, message: '请輸入卡號', trigger: 'blur' },
                { type: 'number', message: '必須為數字'},
            ],
            cardType:{ required: true ,message: '请選擇卡片類型', trigger: 'change' },
            ValidityPeriod:{ required: true ,message: '請輸入有效日期', trigger: 'change' },
            AuthorizationCode:[
                { required: true, message: '请輸入授權碼', trigger: 'blur' },
                { type: 'number', message: '必須為數字'},
            ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
              let paydata = {
                  cardType:this.payFrom.cardType,
                  ValidityPeriod:this.payFrom.ValidityPeriod,
                  AuthorizationCode:this.payFrom.AuthorizationCode,
                  cardNumber:this.payFrom.firstCol+'-'+this.payFrom.secondCol+'-'+this.payFrom.threeCol+'-'+this.payFrom.forthCol
              };
            this.$store.dispatch('addpayment',paydata);
            this.$router.push('/tobackend');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    },
    created(){
        let currentDate = new Date();
        let YYYY = currentDate.getFullYear();
        let MM = currentDate.getMonth();
        for(let i = 0; i < 5 ; i ++){
            
            let YY = YYYY.toString()
            YYYY++;
            console.log(YY)
            for(let j = 0 ; j < 12 ; j++){
                if(j>12) j = 0;
                let mm = j+1;
                let MM = mm.toString()
                this.ValidityDate.push(
                    {
                        year:YY,
                        month:MM
                    }
                )
            }
        }
    }
  }
</script>

