<template>
    <div class="searchDialig">
        <el-form :inline="true" ref="ruleForm" :rules="rules" :model="formInline" class="demo-form-inline">
            <el-form-item class="tourType">
                <el-radio-group v-model="formInline.type">
                    <el-radio class="typeOption" label="roundTrip">來回</el-radio>
                    <el-radio class="typeOption" label="oneWay">單程</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="depAirport">
                <el-select class="depAirport" v-model="formInline.depAirport" placeholder="出發城市,機場">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item prop="arrivedAirport">
                <el-select class="arrivedAirport" v-model="formInline.arrivedAirport" placeholder="出發城市,機場">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item class="selectDate">
                <div class="block">
                    <el-form-item prop="depDate">
                        <el-date-picker type="date" placeholder="請選擇出發日期" v-model="formInline.depDate"></el-date-picker>
                    </el-form-item>
                    <span>至</span>
                    <el-form-item prop="arrivedDate">
                        <el-date-picker type="date" placeholder="請選擇回程日期" v-model="formInline.arrivedDate"></el-date-picker>
                    </el-form-item>
                </div>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('ruleForm')">送出</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        formInline: {
          depAirport:'',
          depDate:'',
          arrivedAirport: '',
          arrivedDate: '',
          type:''
        },
        options: [{
          value: 'TPE',
          label: '台北(桃園)-TPE-台灣'
        }, {
          value: 'KIX',
          label: '大阪-KIX-日本'
        }, {
          value: 'NRT',
          label: '東京(成田)-NRT-日本'
        }, {
          value: 'SIN',
          label: '新加坡-SIN-新加坡'
        }],
        rules:{
            depDate:{type:"date", required: true, message: '請輸入出發日期', trigger: 'change'},
            depAirport:{required: true, message: '請輸入出發城市', trigger: 'change'},
            arrivedDate:{type:"date", required: true, message: '請輸入抵達日期', trigger: 'change'},
            arrivedAirport:{required: true, message: '請輸入抵達城市', trigger: 'change'},
        }
      }
    },
    methods: {
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$store.dispatch('insertSearchTarget',this.formInline)
            this.$router.push('/searchresults')
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

