<template>
    <div class="layout">
        <orderProgress progress="step1">
            <h3 class="title" slot="title">
                <span>1.</span>
                <span>選取</span>
            </h3>
            <div slot="progressLine">progressLine</div>
        </orderProgress>
        <div class="content">
            <div class="showResult">
                <template v-for="item in flight_Information">
                    <Result :props=item :key="item.id"></Result>
                </template>
            </div>
            <div class="order">
                <Cart next='/passengerForm'></Cart>
            </div>
        </div>
    </div>
</template>

<script>
import Result from '../components/ResultTable.vue';
import Cart from '../components/Cart.vue';
import orderProgress from '../components/orderProgress'
export default {
    components:{
        Result,
        Cart,
        orderProgress
    },
    data(){
        // Vuex 取資料的方法 => 到 Vue 實體上找到 store ，在尋找 state 獲取資料
        return{
            receiveSearchTarget:this.$store.state.searchTarget,
            flight_Information: [
                {
                    id: "jbcheufk9y3",
                    Date: 123,
                    DepartureTime: "07:55",
                    Departure: "",
                    ArrivalTime: "11:55",
                    Arrival: "",
                    flightTime: "04:00",
                    Cabin: "",
                    flightType: "直航航班",
                    GoBack:"GO",
                    Price: {
                        economyClass: "4000",
                        businessClass: "10000",
                    },
                    Consignment: {
                        economyClass: false,
                        businessClass: true,
                    },
                    Change: {
                        economyClass: true,
                        businessClass: true,
                    },
                    Return: {
                        economyClass: false,
                        businessClass: true,
                    },
                    SeatSelection: {
                        economyClass: true,
                        businessClass: true,
                    },
                    MileageAccumulation: {
                        economyClass: false,
                        businessClass: true,
                    },
                    MileageUpgrade: {
                        economyClass: false,
                        businessClass: true,
                    }
                }, {
                    id: "jd764bjkocj",
                    Date: "",
                    DepartureTime: "12:55",
                    Departure: "",
                    ArrivalTime: "16:55",
                    Arrival: "",
                    flightTime: "04:00",
                    Cabin: "",
                    flightType: "直航航班",
                    GoBack: "back",
                    Price: {
                        economyClass: "6000",
                        businessClass: "15000",
                    },
                    Consignment: {
                        economyClass: false,
                        businessClass: true,
                    },
                    Change: {
                        economyClass: true,
                        businessClass: true,
                    },
                    Return: {
                        economyClass: true,
                        businessClass: true,
                    },
                    SeatSelection: {
                        economyClass: true,
                        businessClass: true,
                    },
                    MileageAccumulation: {
                        economyClass: false,
                        businessClass: true,
                    },
                    MileageUpgrade: {
                        economyClass: false,
                        businessClass: true,
                    }
                }
            ],
        }
    },
    methods:{
        arrivedDate(){
            let arrivedDate = new Date(this.$store.state.searchTarget.arrivedDate);
            let Year = arrivedDate.getFullYear();
            let Month = arrivedDate.getMonth()+1;
            let date = arrivedDate.getDate();
            let arrDate = `${Year}年${Month}月${date}日`;
            return arrDate;
        },
        depDate(){
            let deportDate = new Date(this.$store.state.searchTarget.depDate);
            let Year = deportDate.getFullYear();
            let Month = deportDate.getMonth()+1;
            let date = deportDate.getDate();
            let depDate = `${Year}年${Month}月${date}日`;
            return depDate;
        },
        switchCountry(site){
            switch(site){
                case 'TPE':
                    return "桃園國際機場 (TPE)";
                break;
                case 'KIX':
                    return "大阪關西機場 (OSK)";
                break;
                case 'NRT':
                    return "東京成田機場(NRT)";
                break;
                case 'SIN':
                    return "新加坡樟宜機場(SIN)";
                break;
            }
            console.log(site)
        },
    },
    created() {
        this.flight_Information[0].Date = this.depDate();
        this.flight_Information[1].Date = this.arrivedDate();
        this.flight_Information[0].Departure = this.switchCountry(this.$store.state.searchTarget.depAirport);
        this.flight_Information[0].Arrival = this.switchCountry(this.$store.state.searchTarget.arrivedAirport);
        this.flight_Information[1].Arrival = this.switchCountry(this.$store.state.searchTarget.depAirport);
        this.flight_Information[1].Departure = this.switchCountry(this.$store.state.searchTarget.arrivedAirport);
        
        
    },
}
</script>


